const state = {
    device: 'desktop',
    size: 0,
    route: 0,
}

const mutations = {
    TOGGLE_DEVICE: (state, device) => {
        state.device = device
    },
    SET_SIZE: (state, size) => {
        state.size = size
    },
    SET_ROUTE: (state, route) => {
        state.route = route
    }
}

const actions = {
    toggleDevice({ commit }, device) {
        commit('TOGGLE_DEVICE', device)
    },
    setSize({ commit }, size) {
        commit('SET_SIZE', size)
    },
    setRoute({ commit }, route) {
        commit('SET_ROUTE', route)
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}
