import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

export const asyncRoutes = [
    // {
    //     path: '/',
    //     name: 'Home',
    //     component: () => import('@/views/Home.vue' /* webpackChunkName: 'home' */ ),
    //     meta: { index: 0, title: '首页', icon: 'icon_homepage' }
    // },
    // {
    //     path: '/game',
    //     name: 'Game',
    //     component: () => import('@/views/Game.vue' /* webpackChunkName: 'game' */ ),
    //     meta: { index: 1, title: '游戏展示', icon: 'icon_sketch' }
    // },
    {
        path: '/privacy',
        name: 'Privacy',
        component: () => import('@/views/Privacy.vue' /* webpackChunkName: 'privacy' */ ),
        meta: { index: 0, title: 'Privacy', icon: 'icon_addresslist' }
    },
    {
        path: '/contact',
        name: 'Contact',
        component: () => import('@/views/Contact.vue' /* webpackChunkName: 'contact' */ ),
        meta: { index: 1, title: 'Contact', icon: 'icon_voipphone' }
    }
]

const routes = [{
        path: '/redirect/:path(.*)',
        component: () => import('@/views/redirect/index'  /* webpackChunkName: 'redirect' */ )
    }, {
        path: '/404',
        component: () => import('@/views/error-page/404'  /* webpackChunkName: '404' */ )
    },


    ...asyncRoutes,

    { path: '/', redirect: { name: "Home" } },

    // 404 page must be placed at the end !!!
    { path: '*', redirect: '/404' }
]

const router = new VueRouter({
    mode: 'history',
    routes
})

export default router
