<template>
    <v-app>

        <my-header v-if="!isMobile"></my-header>
        <mobile-header v-else></mobile-header>

        <v-main>
            <router-view />
        </v-main>

        <my-footer></my-footer>
    </v-app>
</template>

<script>
    const MyHeader = () => import('./components/MyHeader' /* webpackChunkName: 'my-header' */ )
    const MobileHeader = () => import('./components/MobileHeader' /* webpackChunkName: 'mobile-header' */ )

    import MyFooter from './components/MyFooter';

    export default {
        name: 'App',

        components: {
            MyHeader,
            MobileHeader,
            MyFooter
        },

        data: () => ({
            //
        }),
        computed: {
            isMobile() {
                return this.$store.getters.device == 'mobile'
            }
        },
    };
</script>

<style>
    html {
        background-color: #f0f0f0;
    }
</style>
